import React from 'react';
import { Component } from 'react';
import '../Assets/animate.css';
import downloadIcon from '../Assets/emails-interface-download-symbol.png'
class Banner extends Component {
    state = {  }
    iconStyle ={width:"60px"}
    render() { 
        return ( 
           
            <div id="banner" className="animated zoomIn">
                <div className="banerOverlay">
                    <div id="rightButton">
                        <button className="shopNowRightBtn">Get Tickets</button>
                    </div>
                    <div id="leftBannerContent">
                        <h1>Golden Era Festival</h1>
                        <h3 className="pageHeading">About Event</h3>
                        <p>
                        Some description will come underneath this heading.<br>
                        </br>Just a small intro to the site or the brand and a short
                        <br/>info about the artists who'll be attending.
                        </p>
                        <button id="shopNowBtn">
                            Read more.
                        </button>
                        <button id="nextProjBtn">
                            Tickets
                        </button>
                    </div>
                </div>
            </div>
         );
    }
}
 
export default Banner;